<template>
  <Modal
    v-if="show"
    @close="close"
  >
    <template v-slot:header>
      <h1 class="create__heading heading">New task</h1>
    </template>

    <template v-slot:body>
      <div class="create">
        <form
          @submit.prevent="submit"
          class="create__form"
        >
          <div class="create__fields">
            <input
              v-model="title"
              :class="{'textfield__error': error}"
              class="create__field textfield"
              type="text"
              placeholder="Task title"
            >

            <input
              v-for="(todo, index) in todos"
              :key="'todo_' + index"
              v-model="todo.title"
              class="create__field textfield"
              type="text"
              placeholder="Todo"
            >
          </div>

          <button
            @click="more"
            class="create__more button button_icon"
            type="button"
          >
            <svg
              width="16"
              height="16"
            >
              <use xlink:href="#icon-more"></use>
            </svg>
          </button>

          <div class="create__actions">
            <button
              @click="close"
              class="create__cancel button"
              type="button"
            >
              Cancel
            </button>

            <button
              class="create__add button"
              type="submit"
            >
              Add
            </button>
          </div>
        </form>
      </div>
    </template>
  </Modal>
</template>

<script>
import Modal from '@/components/Modal';

export default {
  name: 'CreateTask',

  props: {
    show: {
      type: Boolean,
      default: false
    }
  },

  components: {
    Modal
  },

  data() {
    return {
      error: false,

      title: '',
      todos: [
        {
          title: '',
          completed: false
        }
      ]
    }
  },

  methods: {
    more() {
      this.todos.push({
        title: '',
        completed: false
      })
    },

    submit() {
      if (!this.title) {
        this.error = true;
      } else {
        this.$emit('created', {
          id: Date.now(),
          title: this.title,
          todos: this.todos.filter(todo => todo.title !== '')
        });

        this.close();
      }
    },

    close() {
      this.$emit('close');

      this.error = false;
      this.title = '';
      this.todos = [
        {
          title: '',
          completed: false
        }
      ];
    }
  }
}
</script>

