  &__fields {
    padding: 1rem;
  }

  &__field {
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__more {
    display: block;
    margin: 0 auto 1rem;
  }

  &__actions {
    display: flex;
    justify-content: flex-end;
    padding: 1rem;

    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  &__cancel {
    margin-right: 0.5rem;
  }
}
</style>