<template>
  <div class="list">
    <div class="list__container container">
      <div class="list__header">
        <h1 class="list__heading heading">Tasks</h1>

        <button
          @click="showCreateTask = true;"
          class="list__create button"
          type="button"
          title="Add new task"
        >
          Create new
        </button>
      </div>

      <div
        v-if="tasks.length"
        class="list__items"
      >
        <ListItem
          v-for="task in tasks"
          :key="task.id"
          :task="task"
          @remove="removeTask(task.id)"
        />
      </div>

      <div
        v-else
        class="list__empty"
      >
        No tasks yet
      </div>
    </div>

    <CreateTask
      :show="showCreateTask"
      @created="createTask"
      @close="showCreateTask = false"
    />
  </div>
</template>

<script>
import ListItem from '@/components/ListItem';
import CreateTask from '@/components/CreateTask';

import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'List',
  
  components: {
    ListItem,
    CreateTask
  },
  
  data() {
    return {
      showCreateTask: false
    }
  },

  computed: {
    ...mapGetters({
      tasks: 'GET_TASKS'
    })
  },
  
  methods: {
    ...mapActions({
      createTask: 'CREATE_TASK',
      removeTask: 'REMOVE_TASK'
    })
  }
}
</script>

