<template>
  <div class="list__item task">
    <div class="task__header">
      <h3 class="task__title">{{ task.title }}</h3>

      <div class="task__actions">
        <router-link
          :to="{ path: 'task/' + task.id}"
          class="task__edit button button_icon"
          title="Edit task"
        >
          <svg
            width="16"
            height="16"
          >
            <use xlink:href="#icon-edit"></use>
          </svg>
        </router-link>

        <button
          @click="openRemoveConfirm = true"
          class="task__remove button button_icon button_red"
          type="button"
          title="Remove task"
        >
          <svg
            width="16"
            height="16"
          >
            <use xlink:href="#icon-remove"></use>
          </svg>
        </button>
      </div>
    </div>
    
    <ul
      v-if="todos.length"
      class="task__todos"
    >
      <li
        v-for="(todo, index) in todos"
        :key="task.id + '_' + index"
        :class="{'task__todo_completed': todo.completed}"
        class="task__todo todo"
      >
        {{ todo.title }}
      </li>
    </ul>

    <Confirmation
      :open="openRemoveConfirm"
      @agree="$emit('remove')"
      @close="openRemoveConfirm = false"
    >
      <template v-slot:text>Are you sure you want to delete the task?</template>
    </Confirmation>
  </div>
</template>

<script>
import Confirmation from '@/components/Confirmation';

export default {
  name: 'ListItem',

  components: {
    Confirmation
  },
  
  props: {
    task: {
      type: Object,
      required: true
    }
  },
  
  data() {
    return {
      openRemoveConfirm: false
    }
  },
  
  computed: {
    todos() {
      return this.task.todos.slice(0, 2);
    }
  }
}
</script>

<style lang="less" scoped>
.task {
  padding: 1rem;

  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);

  &__header {
    display: flex;
    align-items: center;
  }

  &__title {
    flex-grow: 1;
    margin: 0 0.5rem 0 0;
    overflow: hidden;

    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__actions {
    white-space: nowrap;
  }

  &__edit {
    margin-right: 0.5rem;
    margin-left: auto;

    background-color: @color_blue;
  }

  &__todos {
    margin: 0.5rem 0 0;
    padding: 1rem 0 0;

    list-style-position: inside;

    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  &__todo {
    margin-bottom: 0.25rem;

    font-size: 0.8rem;
    color: rgba(0, 0, 0, 0.5);

    &_completed {
      text-decoration: line-through;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
</style>